                    </el-tab-pane>
                    <el-tab-pane name="cache" label="">
                        <template>
                            <div class="task-title"></div>

                            <div class="gpu-container" style="margin-top: 12px">
                                <div class="gpu-title"></div>
                                <div class="cache-select-container">
                                    <div class="ts-select-container">
                                        <el-select
                                            v-model="cacheNumber"
                                            placeholder="" class="taskSelect"
                                            style="width: 74px">
                                            <el-option
                                                v-for="(item) in allCacheNumber"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                            </el-option>
                                        </el-select>
                                        <span class="right_icon">
                                            <img :src="$getIconPath('icon_arrow_up.png')" alt="">
                                            <img :src="$getIconPath('icon_arrow_down.png')" alt="">
                                        </span>
                                    </div>
                                    <el-button
                                        class="claRepair"
                                        @click="onClearCache"
