                                            </el-option>
                                        </el-select>
                                        <span class="right_icon" style="left: 54px;">
                                        <img :src="$getIconPath('icon_arrow_up.png')" alt="">
                                        <img :src="$getIconPath('icon_arrow_down.png')" alt="">
                                    </span>
                                    </div>

                                </div>
