                        </template>
                    </el-tab-pane>

                    <el-tab-pane
                        v-if="showExhibitionPane"
                        label=""
                        name="exhibition-config"
                        class="pane-container">
                        <div class="gpu-container">
                            <div class="gpu-title"></div>
                            <el-radio v-model="exhibition.switch" :label="1"></el-radio>
                            <el-radio v-model="exhibition.switch" :label="0"></el-radio>
                        </div>

                        <div class="gpu-container">
                            <div class="gpu-title"></div>
                            <el-input
                                class="import-input"
                                size="mini"
                                placeholder=""
                                v-model.trim="exhibition.importPath">
                                <template slot="append">
                                    <el-button
                                        @click="selectImportPath"
                                        size="mini">
                                        <i class="ts-icon-directory"></i>
                                    </el-button>
                                </template>
                            </el-input>
                        </div>

                        <div class="gpu-container">
                            <div class="gpu-title">Id</div>
                            <el-input
                                v-model.trim="exhibition.presetSuitId"
                                style="width: 100px"
                                size="mini">
                            </el-input>
                        </div>

                        <div class="gpu-container">
                            <div class="gpu-title"></div>
                            <el-input-number
                                size="mini"
                                controls-position="right"
                                style="width: 100px"
                                v-model="exhibition.importDelayTime">
                            </el-input-number>
                            &nbsp;&nbsp;
                        </div>

                        <div style="text-align: right">
